body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  font-size: 16px; /* Bazowa wielkość fontu dla responsywności */
}

header {
  background-color: #333;
  color: white;
  padding: 1.25rem;
  text-align: center;
}

main {
  padding: 1.25rem;
}

nav ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

nav ul li {
  margin: 0 1rem;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 0.625rem;
  position: fixed;
  bottom: 0;
  width: 100%;
}

/* Media queries dla różnych rozdzielczości */
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }

  nav ul li {
    margin: 0.5rem;
  }

  header, footer {
    padding: 0.5rem;
  }
}

@media (max-width: 480px) {
  nav ul {
    flex-direction: column;
  }

  nav ul li {
    margin: 0.5rem 0;
  }
}

/* Stylowanie dla tabletów (od 640px) */
@media (min-width: 640px) {
  nav ul li {
    margin: 10px 20px;
  }
}

/* Stylowanie dla desktopów (od 1024px) */
@media (min-width: 1024px) {
  body {
    font-size: 18px;
  }

  header, footer {
    padding: 30px 50px;
  }

  main {
    padding: 30px 50px;
  }
}
